
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

export default {
components:{
InlineXNumber
},
computed:{
goodsNum() {
if(this.charList.length == 1){
return Number(this.charList[0].count);
}else{
var a =0;
this.charList.map(e =>{
a+= e.count;
})
return a;
}
},
goodsPrice(){
var arr = [...this.charList]
var a =0;
arr.map(e =>{
a+= e.money;
})
return a;
}
},
data() {
return {
mu:0,
ischeck:false,
bottomMsg:{
checkcount:0,
totalprice:0
},
charList:[
{
logo:require('../../../assets/images/WechatIMG99(1).png'),
title:'330g ',
num:1,
id:null,
price:13.91,
ischeck:false,
},
{
logo:require('../../../assets/images/WechatIMG99(1).png'),
title:'330g ',
num:1,
id:null,
price:13.9,
ischeck:false,
}
],
cartDate:{
openId:"1313121231",
id:"",
num:""
}
}
},
methods: {

chooseBuy(item,n) {
item.ischeck = !item.ischeck;
if(item.ischeck){
item.money = (item.count*item.price).toFixed(2)/1;
this.bottomMsg.checkcount++;
}else{
item.money = 0;
this.bottomMsg.checkcount--;
}
this.bottomMsg.totalprice = this.goodsPrice;
this.chart(item,n);
this.$emit('bottomEve',this.bottomMsg)
},

chart(item,n){
var arr = [...this.charList];
this.$emit('receiveArray',arr);
},

change(item,n,arr){
console.log(item,'iipipip');

if(item.count <= 0){
console.log(this.goodsNum);
this.cartDate.id = null;

this.cartDate.num = 0;
this.postCart();
this.$emit('changeNum',this.goodsNum);


return
}else{
this.chart(item,n);
}
if(item.ischeck){
item.money = (item.count * item.price).toFixed(2)/1;
}else{

}


this.bottomMsg.totalprice = this.goodsPrice;
this.postCart();
this.$emit('changeNum',this.goodsNum);
},

getCart(){
this.$fetch.post("fruits/app/cart/getCart",{openId:"1313121231"}).then(res =>{
console.log(res,'dfsf')
res.obj.forEach(e => {
e.ischeck = false;
e.money = 0;
e.price = e.price.toFixed(2);
});
this.charList = res.obj;
this.$emit('package',res.attributes)
})
},

goDetail(state){
if(state == 1){
return
}
},

goShop(){
this.$router.push('/home');
},

postCart(){

this.$fetch.post('fruits/app/cart/changeNum',this.cartDate).then(res =>{
console.log(res,777777777)
})
}
},
mounted() {
this.getCart();

},
}



